<template>
  <div>
    <a-input-search
      v-model:value="SearchValue"
      placeholder="请输入要搜索的商品信息"
      style="width: 200px"
      @search="onSearch"
      allow-clear
    />
     <a-button type="primary" style="margin-left:20px" @click="addGoodsPage">添加商品</a-button>
        <a-table :columns="columns" :data-source="goodsList" rowKey="goods_id" bordered :pagination="false" style="margin:20px 0">
          <template #handler="{text}">
             <a-button type="primary" size="small" @click="showEditGoodsBox(text.goods_id)">编辑</a-button>
             <a-button type="primary" danger size="small" @click="deleteGoods(text.goods_id)">删除</a-button>
        </template>
        <template #time="{text}">
          {{dateFormat(text)}}
        </template>
      </a-table>
            <!-- 分页器 -->
           <a-config-provider :locale="locale">
              <a-pagination @change="pageChangeHandler" @showSizeChange="pageChangeHandler" :total="goodsTotal" :defaultPageSize="5" :pageSizeOptions="['1','2','5','10']" show-size-changer show-quick-jumper />
           </a-config-provider>
  </div>
  <!-- 编辑商品对话框组件-->
    <a-modal v-model:visible="showEditBox" width="1000px" title="编辑分类" @ok="submitEditGoods">
      <p><span>商品名字 : </span><a-input v-model:value="editGoodsList.goods_name" style="width:80%"></a-input></p>
      <p><span>商品价格 : </span><a-input v-model:value="editGoodsList.goods_price" style="width:80%"></a-input></p>
      <p><span>商品重量 : </span><a-input v-model:value="editGoodsList.goods_weight" style="width:80%"></a-input></p>
      <p><span>商品数量 : </span><a-input v-model:value="editGoodsList.goods_number" style="width:80%"></a-input></p>
    </a-modal>
</template>

<script>
import zhCN from 'ant-design-vue/es/locale/zh_CN'
import { getCurrentInstance, onMounted } from 'vue'
import { reactive, ref } from '@vue/reactivity'
import { message } from 'ant-design-vue'
import { dateFormate } from '../assets/js/dateformate'
export default {
  setup () {
    const dateFormat = dateFormate
    const { proxy } = getCurrentInstance()
    // 搜索框绑定的值
    const SearchValue = ref()

    // 表格的数据源
    const goodsList = ref([])
    // 表格渲染规则
    const columns = [
      {
        title: '商品ID',
        dataIndex: 'goods_id'
      },
      {
        title: '商品名字',
        dataIndex: 'goods_name'
      },
      {
        title: '商品价格',
        dataIndex: 'goods_price'
      },
      {
        title: '商品数量',
        dataIndex: 'goods_number'
      },
      {
        title: '商品重量',
        dataIndex: 'goods_weight'
      },
      {
        title: '添加时间',
        dataIndex: 'upd_time',
        slots: { customRender: 'time' }
      },
      {
        title: '操作',
        width: '20%',
        slots: { customRender: 'handler' }
      }
    ]
    // 定义商品总数
    const goodsTotal = ref(0)
    // 请求商品列表所需参数
    const getGoodsParams = {
      query: '',
      pagenum: 1,
      pagesize: 5
    }
    // 搜索框的事件
    const onSearch = (value) => {
      getGoodsParams.query = value
      queryGoodsList()
    }
    // 分页变化时的处理函数
    const pageChangeHandler = (pagenum, pagesize) => {
      getGoodsParams.pagenum = pagenum
      getGoodsParams.pagesize = pagesize
      queryGoodsList()
    }
    // 请求商品列表函数
    const queryGoodsList = async () => {
      const { data } = await proxy.$http.get('goods', {
        params: getGoodsParams
      })
      goodsTotal.value = data.total
      goodsList.value = data.goods
    }
    onMounted(() => {
      queryGoodsList()
    })
    // 点击跳转添加商品页面
    const addGoodsPage = () => {
      proxy.$router.push('addgoods')
    }
    // 删除商品处理函数
    const deleteGoods = async (id) => {
      const { meta } = await proxy.$http.delete(`goods/${id}`)
      if (meta.status != 200) return message.err(meta.msg)
      message.success(meta.msg)
      queryGoodsList()
    }
    // 临时保存编辑框里面的数据
    const editGoodsList = reactive({
      goods_name: '',
      goods_price: '',
      goods_number: '',
      goods_weight: '',
      upd_time: ''

    })
    // 显示编辑对话框变量
    const showEditBox = ref(false)
    // 接收要编辑的ID
    const editID = ref()
    // 显示编辑商品对话框
    const showEditGoodsBox = async (id) => {
      editID.value = id
      const { data } = await proxy.$http.get(`goods/${id}`)
      editGoodsList.goods_name = data.goods_name
      editGoodsList.goods_price = data.goods_price
      editGoodsList.goods_number = data.goods_number
      editGoodsList.goods_weight = data.goods_weight
      editGoodsList.upd_time = Date.now()
      showEditBox.value = true
    }
    // 提交编辑数据
    const submitEditGoods = async () => {
      // const {data,meta} = await proxy.$http.put(`goods/${editID.value}`,editGoodsList)
      message.info('此接口有问题,无法正常修改数据! ! !')
      queryGoodsList()
      showEditBox.value = false
    }
    return {
      SearchValue,
      deleteGoods,
      submitEditGoods,
      showEditBox,
      showEditGoodsBox,
      editGoodsList,
      addGoodsPage,
      pageChangeHandler,
      goodsTotal,
      queryGoodsList,
      goodsList,
      onSearch,
      columns,
      dateFormat,
      locale: zhCN
    }
  }
}
</script>

<style scoped>

</style>
